<template>
  <div class="card">
    <div class="card-body">
      <h2>Disclaimer</h2>
      <p>The information contained on Streamsrip (the "Service") is for general information purposes only.</p>
      <p>Streamsrip assumes no responsibility for errors or omissions in the contents on the Service.</p>
      <p>Reasonable skill and care has been used in producing Streamsrip but it is only designed for general information purposes. No guarantee is given by us or our suppliers that the website (including any statistics contained on the website) is accurate, complete or up-to-date. We therefore disclaim all liability and responsibility arising from any reliance placed on the content of the website by you, or by anyone who may be informed of the website’s contents.</p>
      <p>In no event shall Streamsrip be liable for any special, direct, indirect, consequential, or incidental damages or any damages whatsoever, whether in an action of contract, negligence or other tort, arising out of or in connection with the use of the Service or the contents of the Service. Streamsrip reserves the right to make additions, deletions, or modification to the contents on the Service at any time without prior notice.</p>
    </div>
  </div>
</template>
<script>
  export default {
    name: 'disclaimer',
    metaInfo () {
      return {
        title: 'Disclaimer',
        titleTemplate: '%s | Streamsrip',
        link: [
          { rel: 'canonical', href:window.location.href }
        ],
        meta: [
          { name: 'description', content: 'The information contained on Streamsrip (the "Service") is for general information purposes only.' }
        ]
      }
    },
    data () {
      return {}
    },
  };
</script>



