<template>
  <div class="card">
    <div class="card-body">
      <h2>Privacy Policy</h2>
      <p>This Privacy Policy governs the manner in which Streamsrip collects, uses, maintains and discloses information collected from users (each, a "User") of the Streamsrip website ("Site").</p>
      <h3>Personal identification information</h3>
      <p>We may collect personal identification information from Users in a variety of ways in connection with activities, services, features or resources we make available on our Site. We will collect personal identification information from Users only if they voluntarily submit such information to us. Users can always refuse to supply personally identification information, except that it may prevent them from engaging in certain Site related activities.</p>
      <h3>Non-personal identification information</h3>
      <p>We may collect non-personal identification information about Users whenever they interact with our Site. Non-personal identification information may include the browser name, the type of computer and technical information about Users means of connection to our Site, such as the operating system and the Internet service providers utilized and other similar information.</p>
      <h3>Web browser cookies</h3>
      <p>Our Site may use "cookies" to enhance User experience. User's web browser places cookies on their hard drive for record-keeping purposes and sometimes to track information about them. User may choose to set their web browser to refuse cookies, or to alert you when cookies are being sent. If they do so, note that some parts of the Site may not function properly.</p>
      <h3>How we use collected information</h3>
      <p>Streamsrip may collect and use Users personal information for the following purposes: To run and operate our Site We may need your information display content on the Site correctly. To personalize user experience We may use information in the aggregate to understand how our Users as a group use the services and resources provided on our Site. To improve our Site We may use feedback you provide to improve our products and services. To run a promotion, contest, survey or other Site feature To send Users information they agreed to receive about topics we think will be of interest to them. To send periodic emails We may use the email address to respond to their inquiries, questions, and/or other requests.</p>
      <h3>Our cookies</h3>
      <p>We use cookies to personalise content and ads, to provide social media features and to analyse our traffic. We also share information about your use of our site with our social media, advertising and analytics partners who may combine it with other information that you’ve provided to them or that they’ve collected from your use of their services.</p>
      <h3>How we protect your information</h3>
      <p>We adopt appropriate data collection, storage and processing practices and security measures to protect against unauthorized access, alteration, disclosure or destruction of your personal information, username, password, transaction information and data stored on our Site.</p>
      <h3>Sharing your personal information</h3><p>We do not sell, trade, or rent Users personal identification information to others. We may share generic aggregated demographic information not linked to any personal identification information regarding visitors and users with our business partners, trusted affiliates and advertisers for the purposes outlined above.</p>
      <h3>Third party websites</h3>
      <p>Users may find advertising or other content on our Site that link to the sites and services of our partners, suppliers, advertisers, sponsors, licensors and other third parties. We do not control the content or links that appear on these sites and are not responsible for the practices employed by websites linked to or from our Site. In addition, these sites or services, including their content and links, may be constantly changing. These sites and services may have their own privacy policies and customer service policies. Browsing and interaction on any other website, including websites which have a link to our Site, is subject to that website's own terms and policies.</p>
      <h3>Advertising</h3>
      <p>Ads appearing on our site may be delivered to Users by advertising partners, who may set cookies. These cookies allow the ad server to recognize your computer each time they send you an online advertisement to compile non personal identification information about you or others who use your computer. This information allows ad networks to, among other things, deliver targeted advertisements that they believe will be of most interest to you. This privacy policy does not cover the use of cookies by any advertisers.</p>
      <h3>Google Adsense</h3>
      <p>Some of the ads may be served by Google. Google's use of the DART cookie enables it to serve ads to Users based on their visit to our Site and other sites on the Internet. DART uses "non personally identifiable information" and does NOT track personal information about you, such as your name, email address, physical address, etc. You may opt out of the use of the DART cookie by visiting the Google ad and content network privacy policy at http://www.google.com/privacy_ads.html</p>
      <h3>Changes to this privacy policy</h3>
      <p>Streamsrip has the discretion to update this privacy policy at any time. When we do, we will revise the updated date at the bottom of this page. We encourage Users to frequently check this page for any changes to stay informed about how we are helping to protect the personal information we collect. You acknowledge and agree that it is your responsibility to review this privacy policy periodically and become aware of modifications.</p>
      <h3>Your acceptance of these terms</h3>
      <p>By using this Site, you signify your acceptance of this policy. If you do not agree to this policy, please do not use our Site. Your continued use of the Site following the posting of changes to this policy will be deemed your acceptance of those changes.</p><h3>Contacting us</h3><p>If you have any questions about this Privacy Policy, the practices of this site, or your dealings with this site, please contact us.</p>
    </div>
  </div>
</template>
<script>
  export default {
    name: 'privacy',
    metaInfo () {
      return {
        title: 'Privacy',
        titleTemplate: '%s | Streamsrip',
        link: [
          { rel: 'canonical', href:window.location.href }
        ],
        meta: [
          { name: 'description', content: 'This Privacy Policy governs the manner in which Streamsrip collects, uses, maintains and discloses information collected from users (each, a "User") of the Streamsrip website ("Site").' }
        ]
      }
    },
    data () {
      return {}
    },
  };
</script>



