                <tr v-for='(media,index) in game.content.media.epg[0].items' :class="(feed == media.id && cdn == 'l3c') ? 'active hover' : 'hover'" :key="index+'l3c'" v-on:click="chng(media.id,'l3c')">
                  <td>{{media.mediaFeedType}}</td>
