                  <img :alt="media.description" style="/*padding: 0.25rem;border-radius: 5px;1px solid rgb(3, 15, 29)" v-bind:src="media.image.cuts[15].src">
                  <div class="mt-3">{{ (vidsrc == media.playbacks[1].url) ? 'Playing Now ...' :  media.duration.slice(3)}}</div>
