<script>
import player from '@/components/player'
import chat from '@/components/chat'
import timer from '@/components/timer'
const axios = require('axios')
export default {
  name: 'mlbgame',
  metaInfo () {
    return {
      title: this.title,
      titleTemplate: '%s | Streamsrip',
      meta: [
          { name: 'description', content: 'Watch '+this.title+' For Free On Streamsrip' }
        ],
      link: [
        { rel: 'canonical', href:window.location.href }
      ]
    }
  },
  components: { chat,player,timer },
  data () {
    return {
      chatid:'streamsripnhl',
      date:null,
      d:null,
      game:{},
      title:null,
      gamedate:null,
      lang:{'eng':'English','fra':'French'},
      mstate:{'MEDIA_OFF':'Offline','MEDIA_ARCHIVE':'Archived','MEDIA_ON':'Live'},
      id:this.$route.params.id,
      cdn:this.$route.params.cdn ? this.$route.params.cdn : 'akc',
      feed:this.$route.params.feed ? this.$route.params.feed : 'undefined',
      vidsrc:null,
      play:false,
      renderPage:false,
      error:false,
      timer:true
    }
  },
  mounted(){
    this.Load()
  },
  watch:{
    cdn: function(){
      if(this.feed !== 'undefined'){
        this.play = false
        this.$router.push('/mlb/game/'+this.id+'/'+this.cdn+'/'+this.feed)
        this.loadsrc()
      }
    },
    feed: function(){
      if(this.feed !== 'undefined'){
        this.play = false
        this.$router.push('/mlb/game/'+this.id+'/'+this.cdn+'/'+this.feed)
        this.loadsrc()
      }
    }
  },
  methods: {
    chng(x,y){
      let vm = this
      vm.feed = x
      vm.cdn = y
    },
    chngh(x){
      let vm = this
      vm.vidsrc = x
      vm.timer = false
      vm.play = true
      vm.renderPage = true
    },
    Load(){
      let vm = this
      axios.get('https://statsapi.mlb.com/api/v1/schedule?gamePk='+vm.id+'&expand=schedule.teams,schedule.linescore,schedule.broadcasts,schedule.ticket,schedule.game.content.media.epg&leaderCategories=&site=en_nhl&teamId=')
        .then(function (res) {
          vm.g = res.data.totalGames
          vm.date = res.data.dates[0].date
          vm.game = res.data.dates[0].games[0]
          vm.gamedate = res.data.dates[0].games[0].gameDate
          vm.d = new Date(vm.game.gameDate)
          if((!vm.feed || vm.feed == 'undefined') && vm.game.content.media.epg[0].items[0] && vm.game.status.detailedState  != 'Final')
            vm.feed = vm.game.content.media.epg[0].items[0].id
          vm.title = vm.game.teams.away.team.teamName+' vs '+vm.game.teams.home.team.teamName
          vm.renderPage = true
        })
        .catch(function (err) {
          vm.error = true
        })
    },
    loadsrc(){
      let vm = this
      if(vm.feed != 'undefined' && vm.game.status.detailedState  != 'Final')
      axios.get('http://cdn.streams.rip/mlb/m3u8/'+vm.date+'/'+vm.feed+vm.cdn)
        .then(function (res) {
          vm.vidsrc = res.data
          vm.timer = false
          vm.play = true
        })
        .catch(function (err) {
          //console.log(err)
        })
        vm.renderPage = true
    },
    refresh(){
      this.play = false
      this.$router.push('/mlb/game/'+this.id+'/'+this.cdn+'/'+this.feed)
      this.loadsrc()
    },
    LocalTime(x){
      return LocalTime(x);
    },
    GetLogo(x,y){
      return GetLogo(x,y);
    }
  }
}
</script>

